.footer {
  background-color: $footer-background;
  font-size: 13px;
  text-transform: uppercase;
  line-height: 25px;
  padding-top: 50px;

  @media (max-width: $tablet-width) {
    padding-top: 4px;
  }
}

.footer a, .footer p {
  color: rgba($goods-background, 0.5);
}

.footer a {
  &:hover {
    opacity: 0.7;
  }
}

.footer p {
  margin: 0;
}

.footer h3 {
  text-transform: uppercase;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  font-weight: 400;
  font-family: $font-secondary;
  color: $footer-title;
  margin-bottom: 25px;
}
